<template>
  <div id="app">
    <background :lastId="lastId">
      <game @jokeChange="changeLastId"/>
    </background>
  </div>
</template>

<script>
	import Background from "./components/Background";
	import Game from "./components/Game";
	import 'bulma'


	export default {
		name: 'App',
		data: () => ({
			lastId: 1
		}),
		components: {
			'background': Background,
			'game': Game
		},
    methods: {
			changeLastId() {
				this.lastId = new Date().getTime()
      }
    }
	}
</script>

