<script>
	import jokes from '../jokes.json'
	import {random} from "lodash";

	export default {
		name: 'Game',
		data: () => ({
			joke: null,
			revealed: false,
			loading: false,
			firstJoke: true
		}),
		methods: {
			async generateJoke(event) {
				event && event.preventDefault()

				this.$data.loading = true

				await new Promise(resolve => setTimeout(resolve, 800))
				this.$data.revealed = false
				this.$data.joke = jokes[random(0, jokes.length - 1)]
				this.$data.loading = false
				if (!this.$data.firstJoke) {
					this.$emit('jokeChange')
				}

				this.$data.firstJoke = false

				return false
			},
			reveal() {
				this.$data.revealed = true
			},
		},
		mounted() {
			this.listener = document.addEventListener('keyup', (event) => {

				let spaceKey = 32;
				if (event.keyCode === spaceKey) {
					if (this.$data.joke && !this.$data.revealed) {
						return this.reveal()
					}
					this.generateJoke()
				}
			})

			return false
		},
		beforeDestroy() {
			document.removeEventListener('keyup', this.listener)
		}
	}
</script>

<style>
  .joke {
    margin: 20px 0;
  }
</style>

<template>
  <div>
    <button class="button is-warning" @click="generateJoke" v-if="!joke || revealed">Gerar piada aleatoria</button>
    <div v-if="loading" style="margin: 10px 0">
      <progress class="progress is-large is-info" max="100"/>
    </div>
    <div v-if="joke && !loading">
      <div class="is-size-5 joke">
        {{joke.pergunta}}
      </div>

      <div v-if="revealed">{{ joke.resposta }}</div>

      <button class="button is-primary" v-if="joke.resposta && !revealed" @click="reveal">resposta</button>
    </div>
  </div>
</template>