<script>
	import {random} from 'lodash'

	const colors = [
		'#E9967A',
		'#A9A9A9',
		'#6495ED',
		'#98FB98',
		'#DEB887',
		'#EE82EE',
		'#F08080',
		'#FFA07A',
		'#B0E0E6',
		'#FFE4C4'
	]

	export default {
		name: 'Background',
    props: {
			lastId: {
				type: Number,
      }
    },
		data: () => ({
			backgroundColor: null
		}),
		created() {
			this.changeBackground()
		},
    watch: {
			lastId() {
				this.changeBackground()
      }
    },
    methods: {
			changeBackground() {
				this.backgroundColor = colors[random(0, colors.length - 1)]
			}
    }
	}
</script>

<style>
  html, body, #app, .background {
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
  }

  .background {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
</style>

<template>
  <div :style="{ backgroundColor: backgroundColor }" class="background">
    <slot/>
  </div>
</template>